$mobile: 414px;
$tablet: 768px;
$desktop: 769px;

.hentry .entry-content{
  @media(max-width: $mobile){
    .customer-logo-block{
      $height: 180px;
      .blocks-gallery-grid{
        justify-content: center;
        .blocks-gallery-item{
          margin: .5em 1em;
          width: unset;
          height: $height;
          figure{
            justify-content: center;
            img{
              height: 100%;
              object-fit: contain;      
            }
          }
        }
      }
    }
  }
}
